@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;700&display=swap);
/* general */
html {
  overflow-x: hidden !important;
}

body {
  width: 100%- 10px;
  overflow-x: hidden !important;
  font-family: "Montserrat", sans-serif;
}

.th-roboto {
  font-family: "Roboto", sans-serif;
}

.th-10 {
  font-size: 10px;
}

.th-12 {
  font-size: 12px;
}

.th-13 {
  font-size: 13px;
}

.th-14 {
  font-size: 14px;
}

.th-16 {
  font-size: 16px;
}

.th-18 {
  font-size: 18px;
}

.th-20 {
  font-size: 20px;
}

.th-22 {
  font-size: 22px;
}

.th-24 {
  font-size: 24px;
}

.th-30 {
  font-size: 30px;
}

.th-36 {
  font-size: 36px;
}

.th-72 {
  font-size: 72px;
}

/* font weight */
.th-thin {
  font-weight: 100;
}

.th-light {
  font-weight: 300;
}

.th-regular {
  font-weight: 400;
}

.th-medium {
  font-weight: 500;
}

.th-bold {
  font-weight: 700;
}

/* font color */
.th-black {
  color: #000000 !important;
}

.th-grey {
  color: #959595;
}

.th-white {
  color: #ffffff;
}

.th-primary {
  color: #f66962 !important;
}

/* Link */
.th-link {
  color: #000000;
}

.th-link:hover {
  text-decoration: none;
  color: #f66962;
}
.th-link:focus {
  text-decoration: none;
  color: #f66962;
  box-shadow: none;
}

/* BUTTON */
.th-btn-primary {
  font-size: 12px;
  color: #ffffff;
  background: #f66962;
  border-radius: 3px;
  border: 1px solid #f66962;
  text-transform: uppercase;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 5px;
  padding-right: 5px;
}

.th-btn-primary-outline {
  font-size: 12px;
  color: #f66962;
  background: #ffffff;
  border-radius: 3px;
  border: 1px solid #f66962;
  text-transform: uppercase;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 5px;
  padding-right: 5px;
}
.th-btn-primary-outline:focus {
  color: #f66962;
  box-shadow: none;
}

.th-btn-primary-ghost {
  /* font-size: 16px; */
  color: #f66962;
  background: none;
  border: none;
  text-transform: uppercase;
}

.th-btn-primary:focus,
.th-btn-primary-outline:focus,
.th-btn-primary-ghost:focus {
  outline: none;
}

/* BG Color */
.th-bg-white {
  background: #ffffff;
}
.th-bg-primary {
  background: #f66962;
}

/* BG Color Neutral */

.th-bg-black {
  background: #000000;
}

.th-bg-white {
  background: #ffffff;
}

/* Header */
.th-header-logo {
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
  height: 24px;
  mix-blend-mode: luminosity;
}

/* Footer */
.th-footer-logo {
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
  height: 32px;
}

@media screen and (max-width: 767.5px) {
  .th-hidden-sm {
    display: none !important;
  }
}

@media screen and (min-width: 768px) {
  .th-hidden-md {
    display: none !important;
  }
}


.th-profile-avatar {
  height: 150px;
  width: 150px;
  background-position: center;
  background-size: cover;
  border-radius: 75px;
  border: 5px solid #4a65c0;
}

@media (max-width: 767px) {
  .th-profile-avatar {
    height: 80px;
    width: 80px;
    border-radius: 40px;
    border: 2px solid #4a65c0;
  }
}

.th-gallery {
  -webkit-column-count: 3;
          column-count: 3;
  grid-column-gap: 16px;
  -webkit-column-gap: 16px;
          column-gap: 16px;
}
.th-gallery img {
  width: 100%;
  margin: 7px 0;
}

.th-gallery img:hover {
  box-shadow: 0px 0px 10px 1px rgba(74, 101, 192, 0.5);
  cursor: pointer;
}

@media (max-width: 500px) {
  .th-gallery {
    -webkit-column-count: 2;
            column-count: 2;
    grid-column-gap: 4px;
    -webkit-column-gap: 4px;
            column-gap: 4px;
  }

  .th-gallery img {
    margin: 2px 0;
  }
}

.th-gallery-backdrop {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(32, 33, 38, 0.6);
  z-index: 100;
}

.th-slider-container {
  z-index: 200px;
  position: absolute;
  top: 20vh;
  left: 0vw;
  right: 0vw;
}

.th-slider-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 450px;
}

